import React from 'react';
// npm install --save-dev @iconify/react @iconify/icons-fa-brands
import { Icon, InlineIcon } from '@iconify/react';
import tiktokIcon from '@iconify/icons-fa-brands/tiktok'; //Icone Tiktok
import Fade from 'react-reveal/Fade';

class Footer extends React.Component {

    render() {

        return(

            <div id="contact">
                        <footer class="footer text-center">
                    <div class="container">
                        <div class="row">
                            {/* <!-- Footer Location--> */}
                            <div class="col-lg-4 mb-5 mb-lg-0">
                            </div>
                            {/* <!-- Footer Social Icons--> */}
                            <div class="col-lg-4 mb-5 mb-lg-0">
                                <h4 class="text-uppercase  goldenTruth" style={{fontSize: '36px'}}>Contact</h4>
                                <div class="divider-custom divider-light">
                                <div class="divider-custom-line mb-3"></div>
                                </div>
                                
                                <Fade right delay='300'>
                                <div className="ba b--transparent ph0 mh0">
                                <a className="b center center ph3 pv2 input-reset ba b--black bg-transparent grow pointer f6 dib " href='https://www.tiktok.com/@_draw_my_anime_?' style= {{textDecoration:'none', color:'#FAF90D', borderColor:'transparent'}}>
                                <Icon icon={tiktokIcon} style={{fontSize: '2rem',color:'#FAF90D'}} />
                                <h5 class=" mt-2">_draw_my_anime_</h5> </a>
                               </div>
                               </Fade>
                            </div>
                            {/* <!-- Footer About Text--> */}
                            <div class="col-lg-4">
                            </div>
                        </div>
                    </div>
                </footer>
                {/* <!-- Copyright Section--> */}
                <section class="copyright py-4 text-center text-white">
                    <div class="container"><small>Draw My Anime</small></div>
                </section>
                {/* <!-- Scroll to Top Button (Only visible on small and extra-small screen sizes)--> */}
                <div class="scroll-to-top d-lg-none position-fixed">
                    <a class="js-scroll-trigger d-block text-center text-white rounded" href="#page-top"><i class="fa fa-chevron-up"></i></a>
                </div>

            </div>
        )
    }

}

export default Footer;