import React from 'react';
import Fade from 'react-reveal/Fade';
import d0 from './img/d0.jpg'
import d1 from './img/d1.jpg'
import d2 from './img/d2.jpg'


const Dessins = [
        {
            img : d0,
            title: 'Eren VS Reiner'
        },
        {
            img : d1,
            title: 'Todoroki VS Midoriya'
        },
        {
            img : d2,
            title: 'HAHghjhgA'
        }
        
    ]


class TimeLapseSection extends React.Component {

    render() {

        return (

        <section className="page-section portfolio bg-third" id="time-laps">
            <div className="container">
                {/* <!-- Portfolio Section Heading--> */}
                <div style ={{display: 'inline-flex'}}>
               
                <h2 className="page-section-heading text-center text-uppercase text-secondary mb-0 goldenTruth">Time-lapse</h2> 
                
                </div>
                {/* <!-- Icon Divider--> */}
                <div className="divider-custom">
                    <div className="divider-custom-line"></div>
                </div>
                {/* <!-- Portfolio Grid Items--> */}
                <div className="row">

            {Dessins.map((value,index)=>{
                    return( 
                        <Fade bottom delay={300 + index*400} stretched >
                        <div className="col-md-6 col-lg-4 mb-5">
                            <div className="portfolio-item mx-auto grow" data-toggle="modal" data-target={`#portfolioModal${index}`}>
                                <div className="portfolio-item-caption d-flex align-items-center justify-content-center h-100 w-100">
                                    <div className="portfolio-item-caption-content text-center text-white"><i className="fas fa-plus fa-3x"></i></div>
                                </div>
                                <img className="img-fluid" src={value.img}  alt="" />
                            </div>
                        </div>        
                        </Fade>
                    )})
                    
            }
        
            </div> 

            {Dessins.map((value,index)=>{
                    return( 
                        <div className="portfolio-modal modal fade" id={`portfolioModal${index}`} tabIndex="-1" role="dialog" aria-labelledby={`portfolioModal${index}Label`} aria-hidden="true">
                        <div className="modal-dialog modal-xl" role="document">
                            <div className="modal-content">
                                <button className="close" type="button" data-dismiss="modal" aria-label="Close">
                                    <span aria-hidden="true"><i className="fas fa-times"></i></span>
                                </button>
                                <div className="modal-body text-center">
                                    <div className="container">
                                        <div className="row justify-content-center">
                                            <div className="col-lg-8">
                                                {/* <!-- Portfolio Modal - Title--> */}
                                                <h2 className="portfolio-modal-title text-secondary text-uppercase mb-0">{value.title}</h2>
                                                {/* <!-- Icon Divider--> */}
                                                <div className="divider-custom">
                                                    <div className="divider-custom-line"></div>
                                                </div>
                                                {/* <!-- Portfolio Modal - Image--> */}
                                                <img className="img-fluid rounded mb-5 mask" src={value.img}  alt="" style={{pointerEvents:'none'}} />
                                                {/* <!-- Portfolio Modal - Text--> */}
                                                <button className="btn btn-primary" href="#" data-dismiss="modal"><i className="fas fa-times fa-fw"></i>Fermer</button>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        </div>
                        

 
                    )})
                    
            }                     



</div>
</section>

        )

}

}

export default TimeLapseSection;
