import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faStar} from '@fortawesome/free-solid-svg-icons'
import background from './background2.jpg'

class MasterHead extends React.Component {

    render() {

        return (             
            <header class="masthead bg-sky text-white text-center">  
                <div class="container d-flex align-items-center flex-column" >        
                    
                <div className="centered" >

                <div class="divider-custom divider-light">
                            <div class="divider-custom-line"></div>
                        </div>


                        <div style ={{display: 'inline-flex'}}>
                        
                        <FontAwesomeIcon  icon={faStar} className = "sideIcons"/>
                        
                            <h1 class="masthead-heading text-uppercase mb-0 mt-0 MainTitle">Draw My Anime</h1>
                            
                        <FontAwesomeIcon  icon={faStar} className = "sideIcons"/>

                        </div>

                        {/* <!-- Icon Divider--> */}
                        <div class="divider-custom divider-light">
                            <div class="divider-custom-line"></div>
                        </div>
                        {/* <!-- Masthead Subheading--> */}
                        <div style ={{display: 'inline-flex'}}>
                        <p class="masthead-subheading font-weight-light mb-0"></p>
                        </div>
                </div>
             </div>
            
        </header>
        
        );

    }

}


export default MasterHead;



