import React from 'react';
import { NavHashLink } from 'react-router-hash-link';
import './Navigation.css'

import Shape from '../Shape/Shape'


class Navigation extends React.Component {

    render() {

        return (
        <nav className="navbar navbar-expand-lg bg-secondary text-uppercase fixed-top" id="mainNav">
                    
                    
                    <NavHashLink
                    
						smooth
							to="/#page-top"
							className="navbar-brand js-scroll-trigger"
							// activeStyle={{ color: '#2bbc8a'}}
							><Shape/></NavHashLink><button className="navbar-toggler navbar-toggler-right text-uppercase font-weight-bold bg-primary text-white rounded" type="button" data-toggle="collapse" data-target="#navbarResponsive" aria-controls="navbarResponsive" aria-expanded="false" aria-label="Toggle navigation">Menu <i class="fas fa-bars"></i></button>
                      
                      <div className="collapse navbar-collapse" id="navbarResponsive">
                        <ul className="navbar-nav">
                        <li className="nav-item mx-0 mx-lg-1"><NavHashLink  
                                smooth
                                    to="/#page-top"
                                    className="navbar-brand grow"
                                    // activeStyle={{ color: '#2bbc8a'}}
                                    >Draw My Anime</NavHashLink></li>
                                    
                                    </ul>
                            
                          </div>
                          
                      

                        <div className="collapse navbar-collapse" id="navbarResponsive">
                            <ul className="navbar-nav ml-auto">


                            <li className="nav-item mx-0 mx-lg-1 grow"><NavHashLink  
                                smooth
                                    to="/#portfolio"
                                    className="nav-link py-3 px-0 px-lg-3 rounded js-scroll-trigger  "
                                    // activeStyle={{ color: '#2bbc8a'}}
                                    >Portfolio</NavHashLink></li>




                            <li className="nav-item mx-0 mx-lg-1 grow"><NavHashLink
						smooth
							to="/#time-laps"
							className="nav-link py-3 px-0 px-lg-3 rounded js-scroll-trigger"
							// activeStyle={{ color: '#2bbc8a'}}
							>Time-Lapse</NavHashLink></li>


                        <li className="nav-item mx-0 mx-lg-1 grow"><NavHashLink          
						smooth
							to="/#about"
							className="nav-link py-3 px-0 px-lg-3 rounded js-scroll-trigger"
							// activeStyle={{ color: '#2bbc8a'}}
							>About me</NavHashLink></li>


                        <li className="nav-item mx-0 mx-lg-1 grow"><NavHashLink          
						smooth
							to="/#contact"
							className="nav-link py-3 px-0 px-lg-3 rounded js-scroll-trigger"
							// activeStyle={{ color: '#2bbc8a'}}
							>Contact</NavHashLink></li>
                            

                            </ul>
                        </div>
                    
                </nav>
        );

    }

}


export default Navigation;